// ----------------------------------------------
// Identify this css file as the main editor css file
// ----------------------------------------------
$asset-path-prefix: './';

// ----------------------------------------------
// Core helpers
// ----------------------------------------------
@import './core';

// CSS output begins here

// ----------------------------------------------
// Generic
// Custom properties, normalize, font-face, etc.
// ----------------------------------------------
@import './styles/4-common/normalize';
@import './styles/4-common/custom-properties';
@import './styles/4-common/font-face';
// @import './styles/4-common/focus';
@import './styles/4-common/selection';
@import './styles/4-common/box-sizing';

// ----------------------------------------------
// Elements
// Bare HTML elements
// ----------------------------------------------
body {
    // gets converted to .editor-styles-wrapper
    --block--margin-top: var(--space--xl);
    --block--margin-bottom: var(--space--xl);

    @include typestyle-base;

    &:not(.mce-content-body) {
        background-color: var(--color--background);
        color: var(--color--foreground);
    }
}

@import './styles/5-elements/a-elements';
@import './styles/5-elements/blockquote-elements';
